import React, { useState } from "react";
import Modal from "../Modal";
import { format } from "date-fns";
import { utcToZonedTime } from "date-fns-tz";

const AdminFlightsCard = ({ flight }) => {
  const [viewPrice, setViewPrice] = useState(false);
  const [deleteFlightId, setDeleteFlightId] = useState("");
  const [isModelOpen, setIsModelOpen] = useState(false);

  const handleViewPrice = (e) => {
    e.preventDefault();
    setViewPrice(!viewPrice);
  };

  let arrivalTime = new Date(flight.arrivalDate);
  let departureTime = new Date(flight.departureDate);

  let utcDate = arrivalTime;
  let istDate = utcToZonedTime(utcDate, "Asia/Kolkata");
  arrivalTime = format(istDate, "HH:mm");

  utcDate = departureTime;
  istDate = utcToZonedTime(utcDate, "Asia/Kolkata");
  departureTime = format(istDate, "HH:mm");

  const deleteFlight = async () => {
    const response = await fetch(`/api/v1/flights/${deleteFlightId}`, {
      method: "DELETE",
    });
    const data = await response.json();
    if (data.status === "success") {
      setIsModelOpen(false);
      window.location.reload();
    }
  };

  const handleDeleteFlight = (e) => {
    e.preventDefault();
    deleteFlight();
  };

  return (
    <>
      <div className="flight-details-card">
        <div className="flight-card-container">
          <div className="flight-card-flght-img-name">
            <img
              className="flight-details-card-img"
              src="data:image/svg+xml;base64,"
              alt="aeroplane-logo"
            />
            <div>
              <p className="flight-details-flight-name">{flight.name}</p>
              <span>{flight.flightNumber}</span>
            </div>
          </div>
          <div>
            <span className="flight-details-time-name">Departure : </span>
            <p className="flight-details-time">{departureTime}</p>
            <span>{flight.originPlace}</span>
          </div>
          <div>
            <span className="flight-details-time-name">Arrival :</span>
            <p className="flight-details-time">{arrivalTime}</p>
            <span> {flight.destinationPlace}</span>
          </div>

          <div className="flight-details-admin-button-cotnainer">
            <button
              className="flight-details-book-button"
              onClick={handleViewPrice}
            >
              View Prices
              {!viewPrice ? (
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  fill="none"
                  viewBox="0 0 24 24"
                  strokeWidth={1.5}
                  stroke="currentColor"
                  className="icon"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    d="M19.5 8.25l-7.5 7.5-7.5-7.5"
                  />
                </svg>
              ) : (
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  fill="none"
                  viewBox="0 0 24 24"
                  strokeWidth={1.5}
                  stroke="currentColor"
                  className="icon"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    d="M4.5 15.75l7.5-7.5 7.5 7.5"
                  />
                </svg>
              )}
            </button>
            <button
              className="flight-details-cancel-button"
              onClick={() => {
                setIsModelOpen(true);
                setDeleteFlightId(flight._id);
              }}
            >
              Cancel Flight
            </button>
          </div>
        </div>
        {viewPrice && (
          <div className="flight-details-price-card">
            <div>
              <table className="flight-details-table">
                <thead>
                  <tr>
                    <th className="flight-details-thead">FARES</th>
                    <th className="flight-details-thead">CHECK-IN</th>
                    <th className="flight-details-thead">CANCELLATION</th>
                    <th className="flight-details-thead">BOOKING CHARGES</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td className="flight-details-class flight-details-values">
                      Economy
                    </td>
                    <td className="flight-details-values">25 kg</td>
                    <td className="flight-details-values">Free Cancellation</td>
                    <td className="flight-details-values flight-details-booking">
                      <p className="flight-detials-table-price">
                        ₹ {flight.economyPrice}
                      </p>
                    </td>
                  </tr>
                  <tr>
                    <td className="flight-details-class flight-details-values">
                      First Class
                    </td>
                    <td className="flight-details-values">25 kg</td>
                    <td className="flight-details-values">Free Cancellation</td>
                    <td className="flight-details-values flight-details-booking">
                      <p className="flight-detials-table-price">
                        ₹ {flight.firstClassPrice}
                      </p>
                    </td>
                  </tr>
                  <tr>
                    <td className="flight-details-class flight-details-values">
                      Premium
                    </td>
                    <td className="flight-details-values">25 kg</td>
                    <td className="flight-details-values">Free Cancellation</td>
                    <td className="flight-details-values flight-details-booking">
                      <p className="flight-detials-table-price">
                        ₹ {flight.premiumClassPrice}
                      </p>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        )}
      </div>
      {isModelOpen && (
        <Modal isModalOpen={isModelOpen} setIsModalOpen={setIsModelOpen}>
          <p className="model-booking-cancel-text">
            Are you sure !, You want to cancel the Flight
          </p>
          <div className="model-booking-cancel-button-container">
            <button
              className="model-booking-yes-button"
              onClick={(e) => {
                handleDeleteFlight(e);
                setIsModelOpen(false);
                setDeleteFlightId("");
              }}
            >
              Yes
            </button>
            <button
              className="model-booking-no-button"
              onClick={() => {
                setIsModelOpen(false);
                setDeleteFlightId("");
              }}
            >
              No
            </button>
          </div>
        </Modal>
      )}
    </>
  );
};

export default AdminFlightsCard;
